import Exercice from '../Exercice.js'
import { ajouteChampTexte, setReponse } from '../../modules/gestionInteractif.js'
import { listeQuestionsToContenu, randint, range1, combinaisonListes, texNombrec, texNombre } from '../../modules/outils.js'
export const titre = 'Moiti√©'
export const amcReady = false
export const interactifReady = true
export const amcType = 4
/**
 * Calculer la moiti√© d'un nombre pair, d'un impair inf√©rieur √† 20, d'un multiple de 200, d'un nombre de la forme a00 avec a impair, d'un nombre de la forme
 *  a,b avec a et b pairs ou 1xx avec xx un nombre pair
 * @Auteur R√©mi Angot
 * R√©f√©rence CM009
*/
export default function Moitie () {
  Exercice.call(this) // H√©ritage de la classe Exercice()
  this.titre = titre
  this.amcReady = amcReady
  this.amcType = amcType
  this.interactifReady = interactifReady
  this.consigne = 'Calculer'
  this.nbQuestions = 10
  this.nbCols = 2
  this.nbColsCorr = 2
  this.sup = 1 // niveau de difficult√©
  this.tailleDiaporama = 100

  this.nouvelleVersion = function () {
    this.listeQuestions = [] // Liste de questions
    this.listeCorrections = [] // Liste de questions corrig√©es

    const typesDeQuestionsDisponibles = range1(6)
    const listeTypeDeQuestions = combinaisonListes(
      typesDeQuestionsDisponibles,
      this.nbQuestions
    ) // Tous les types de questions sont pos√©es mais l'ordre diff√®re √† chaque "cycle"
    for (
      let i = 0, texte, texteCorr, a, b, cpt = 0;
      i < this.nbQuestions && cpt < 50;

    ) {
      switch (listeTypeDeQuestions[i]) {
        case 1: // Table de 2
          a = randint(2, 9)
          texte = `$\\text{La moiti√© de }${a * 2}$`
          texteCorr = `$\\text{La moiti√© de }${a * 2} \\text{ est } ${a}$`
          setReponse(this, i, a)
          if (this.interactif) texte += ajouteChampTexte(this, i)
          break
        case 2: // Impair inf√©rieur √† 20
          a = randint(2, 9)
          texte = `$\\text{La moiti√© de }${a * 2 + 1}$`
          texteCorr = `$\\text{La moiti√© de }${a * 2 + 1
            } \\text{ est } ${texNombrec(a + 5 / 10)}$`
          setReponse(this, i, texNombrec(a + 5 / 10))
          if (this.interactif) texte += ajouteChampTexte(this, i)
          break
        case 3: // Table de 200
          a = randint(2, 9)
          texte = `$\\text{La moiti√© de }${texNombre(a * 2 * 100)}$`
          texteCorr = `$\\text{La moiti√© de }${texNombre(
            a * 2 * 100
          )} \\text{ est } ${texNombre(a * 100)}$`
          setReponse(this, i, texNombre(a * 100))
          if (this.interactif) texte += ajouteChampTexte(this, i)
          break
        case 4: // a00 avec a impair
          a = randint(2, 9)
          texte = `$\\text{La moiti√© de }${texNombre((a * 2 + 1) * 100)}$`
          texteCorr = `$\\text{La moiti√© de }${texNombre(
            (a * 2 + 1) * 100
          )} \\text{ est } ${texNombre(a * 100 + 50)}$`
          setReponse(this, i, texNombre(a * 100 + 50))
          if (this.interactif) texte += ajouteChampTexte(this, i)
          break
        case 5: // a,b avec a et b pairs
          a = randint(2, 9)
          b = randint(2, 9)
          texte = `$\\text{La moiti√© de }${texNombrec(
            a * 2 + (b * 2) / 100
          )}$`
          texteCorr = `$\\text{La moiti√© de }${texNombrec(
            a * 2 + (b * 2) / 100
          )} \\text{ est } ${texNombrec(a + b / 100)}$`
          setReponse(this, i, texNombrec(a + b / 100))
          if (this.interactif) texte += ajouteChampTexte(this, i)
          break
        case 6: // 1xx avec xx un nombre pair
          a = randint(2, 9)
          texte = `$\\text{La moiti√© de }${100 + a * 2}$`
          texteCorr = `$\\text{La moiti√© de }${100 + a * 2} \\text{ est } ${50 + a
            }$`
          setReponse(this, i, 50 + a)
          if (this.interactif) texte += ajouteChampTexte(this, i)
          break
      }

      if (this.listeQuestions.indexOf(texte) === -1) {
        // Si la question n'a jamais √©t√© pos√©e, on en cr√©e une autre
        this.listeQuestions.push(texte)
        this.listeCorrections.push(texteCorr)
        i++
      }
      cpt++
    }
    listeQuestionsToContenu(this)
  }
  // this.besoinFormulaireNumerique = ['Niveau de difficult√©',3];
}
